import React, { Component, Fragment } from 'react'
import { Row, Col, Card, Table } from 'antd'
import Filter from "./Filter"
import { postAction } from "../../../axios"
import { setLocal, getLocal} from "../../../utils/index"

import '../all/style.less'

class Personal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            dataSource: [],
            pagination: {
                current: 1, // 当前页数，
                pageSize: 10, // 每页条数
                total: 0, // 数据总条数
                showTotal: (total) => `共${total}条数据`, //显示数据总条数
                showQuickJumper: true // 显示页码快速跳转
            },
            filterValue: {}
        }
        this.getFilterValueAndName = this.getFilterValueAndName.bind(this)
    }

    //获取学生列表
    getStudentList() {
        const { filterValue: { curGrade, curCollege, curMajor, curClass, studentNo, gender }, pagination: { current, pageSize } } = this.state
        postAction(
            "/bigdata/student/getStudentInfoPager",
            {
                collegeCode: curCollege,
                grade: curGrade,
                majorCode: curMajor,
                classCode: curClass,
                studentNo: studentNo,
                gender: gender,
                pageInfo: {
                    "pageNum": current,
                    "pageSize": pageSize
                }
            }
        ).then(
            (res) => {
                if (res.success) {
                    if (res.obj.list && res.obj.list.length > 0) {
                    	
                        res.obj.list.map(
                        	(item, index) => {
	                        	item.key = index + 1;
	                        	item.sortIndex = index + 1;
	                        	return item
                        	}
                        )
                        const { pageNum, pageSize, total } = res.obj
                        
                        this.setState({
                            dataSource: res.obj.list,
                            pagination: {
                                current: pageNum,
                                pageSize: pageSize,
                                total: total,
                                showTotal: (total) => `共${total}条数据`,
                                showQuickJumper: true
                            }
                        })
                    } else {
                    	this.setState({
                            dataSource: [],
                            pagination: {
				                current: 1, // 当前页数，
				                pageSize: 10, // 每页条数
				                total: 0, // 数据总条数
				                showTotal: (total) => `共${total}条数据`, //显示数据总条数
				                showQuickJumper: true // 显示页码快速跳转
				            }
                        })
                    }
                }
            }
        )
    }

    getFilterValueAndName(obj) {
        this.setState(
            {
                filterValue: obj
            },
            () => {
                this.getStudentList()
            }
        )
    }

    goToDetail(item) {
        setLocal('studentItem', JSON.stringify(item))
        this.props.history.push('/mainframe/xingweihuaxiang/personal/details')
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 当前页数，
                        pageSize: pagination.pageSize, // 每页条数
                        total: pagination.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`,
                        showQuickJumper: true // 显示页码快速跳转
                    }
                }
            },
            () => {
                this.getStudentList();
            }
        )
    }

    componentDidMount() {
        let isAdmin = JSON.parse(getLocal('isAdmin'))
        if(!isAdmin.student){
            this.getStudentList()
        }else{
            setLocal('studentItem', JSON.stringify(isAdmin.personalPortraitDetailsData))
            this.props.history.push('/mainframe/xingweihuaxiang/personal/details')
        }
        
    }

    render() {

        const columns = [
            { title: '序号', dataIndex: 'sortIndex', width: '6%', align: 'center' },
            { title: '学号', dataIndex: 'studentId', width: '13%', align: 'center' },
            { title: '一卡通', dataIndex: 'studentNo', width: '13%', align: 'center' },
            { title: '姓名', dataIndex: 'studentName', width: '13%', align: 'center' },
            { title: '性别', dataIndex: 'gender', width: '6%', align: 'center' },
            { title: '院系', dataIndex: 'collegeName',width: '13%', align: 'center' },
            { title: '专业', dataIndex: 'majorName', width: '13%', align: 'center' },
            { title: '班级', dataIndex: 'className', width: '10%', align: 'center' },
            { title: '年级', dataIndex: 'currentGrade', width: '10%', align: 'center' },
            {
                title: '操作', dataIndex: 'handle', width: '15%', align: 'center',
                render: (text, record) => {
                    return (
                        <span style={{ cursor: "pointer", color: "#1890ff" }} onClick={() => { this.goToDetail(record) }}>查看</span>
                    )
                }
            }
        ]

        return (
            <Fragment>
                <Row style={{ margin: '-20px' }}>
                    <Col span={24} className="filter-height">
                        <Filter getFilterValueAndName={this.getFilterValueAndName} />
                    </Col>
                </Row>

                <Row style={{ margin: '0px -20px' }}>
                    <Col span={24} className="card-padding">
                        <Card
                            title={'选择学员'}
                            headStyle={{ border: 'none' }}
                            bodyStyle={{ paddingTop: 0 }}
                        >
                            <Table
                                dataSource={this.state.dataSource}
                                pagination={this.state.pagination}
                                columns={columns}
                                bordered={true}
                                size="middle"
                                onChange={this.handleTableChange.bind(this)}
                            />
                        </Card>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}

export default Personal



