import React, { Component, Fragment } from 'react';
import { Select, Button, Input } from 'antd';
import { postAction } from "../../../axios";

const curYear = new Date().getFullYear();
const Option = Select.Option;
export default class Filter extends Component {

    state = {
        college: '',
        collegeLists: [],
        profession: '',
        professionList: [],
        curGrade: '',
        yearLists: [],
        curClass: '',
        classLists: [],
        studentNo: '',
        curYear: curYear + '',
        gender: ''
    }

    // 获取学院list
    getColledgeLists() {
        postAction("/bigdata/school/listCollege").then((res) => {
            if (res.success) {
                this.setState({
                    college: '',
                    collegeLists: res.obj
                })
            }
        })
    }

    // 获取专业list
    getMajorLists() {
        postAction(
            "/bigdata/school/listMajorByCollege",
            {
                collegeCode: this.state.college
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        profession: "",
                        professionList: res.obj
                    })
                }
            }
        )
    }

    // 获取年级list
    getGradeLists() {
        postAction(
            "/bigdata/school/getGradeList",
            {
                year: this.state.curYear,
                collegeCode: this.state.college,
                majorCode: this.state.profession
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        curGrade: "",
                        yearLists: res.obj
                    })
                }
            }
        )
    }

    // 获取班级list
    getClassLists() {
        postAction(
            "/bigdata/school/listClassByMajor",
            {
                year: this.state.curGrade,
                majorCode: this.state.profession
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        curClass: "",
                        classLists: res.obj
                    })
                }
            }
        )
    }

    //筛选条件
    changeFilter = (num, value, option) => {
        if (num === 1) { //学院
            this.setState(
                {
                    college: value,
                    curCollegeName: option.props.children,
                },
                () => {
                    this.getMajorLists()
                    this.getGradeLists()
                    this.getClassLists()
                }
            )
        } else if (num === 2) { //专业
            this.setState(
                {
                    profession: value,
                    professionName: option.props.children,
                },
                () => {
                    this.getGradeLists()
                    this.getClassLists()
                }
            )
        } else if (num === 3) { //年级
            this.setState(
                {
                    curGrade: value,
                    curYear: value,
                    curGradeName: option.props.children,
                },
                () => {
                    this.getClassLists()
                }
            )
        } else if (num === 4) { //班级
            this.setState({
                curClass: value,
                curClassName: option.props.children,
            })
        }
    }

    studyNum(event) {
        this.setState({ studentNo: event.target.value });
    }

    //点击搜索
    searchMore() {
        const { college, profession, curGrade, curClass, studentNo, gender } = this.state
        let obj = {
            curCollege: college,
            curMajor: profession,
            curGrade: curGrade,
            curClass: curClass,
            studentNo: studentNo,
            gender: gender
        };
        this.props.getFilterValueAndName(obj);
    }

    //选择性别
    handleChange(value) {
        this.setState({ gender: value })
    }

    componentDidMount() {
        this.getColledgeLists()
        this.getMajorLists()
        this.getGradeLists()
        this.getClassLists()
    }

    render() {
        const { college, collegeLists, profession, professionList, curGrade, yearLists, curClass, classLists, studentNo } = this.state
        return (
            <Fragment>
                <span className="filter-title">选择条件：</span>
                <Select
                    value={college}
                    placeholder="请选择学院"
                    style={{ width: 135 }}
                    onChange={this.changeFilter.bind(this, 1)}
                >
                    {
                        collegeLists.map((item, index) => (
                            <Option
                                key={index}
                                title={item.collegeName}
                                value={item.collegeCode}
                            >
                                {item.collegeName}
                            </Option>
                        ))
                    }
                </Select>

                <Select
                    value={profession}
                    placeholder="请选择专业"
                    style={{ width: 135 }}
                    onChange={this.changeFilter.bind(this, 2)}
                >
                    {
                        professionList.map((item, index) => (
                            <Option
                                key={index}
                                title={item.majorName}
                                value={item.majorCode}
                            >
                                {item.majorName}
                            </Option>
                        ))
                    }
                </Select>

                <Select
                    value={curGrade}
                    onChange={this.changeFilter.bind(this, 3)}
                >
                    {
                        yearLists.map((item, index) =>
                            (
                                <Option key={index} value={item.name}>{item.value}</Option>
                            ))
                    }
                </Select>

                <Select
                    value={curClass}
                    placeholder="请选择班级"
                    style={{ width: 120 }}
                    onChange={this.changeFilter.bind(this, 4)}
                >
                    {
                        classLists.map((item, index) => (
                            <Option key={index} title={item.className} value={item.classCode}>{item.className}</Option>
                        ))
                    }
                </Select>

                <Select defaultValue="" style={{ width: 100 }} onChange={this.handleChange.bind(this)}>
                    <Option value="">不分性别</Option>
                    <Option value="男">男</Option>
                    <Option value="女">女</Option>
                </Select>

                <Input placeholder="请输入学生姓名或学号" value={studentNo} onChange={this.studyNum.bind(this)} style={{ width: 150, marginRight: 10 }} />

                <Button type="primary" icon="search" onClick={() => this.searchMore()}>搜  索 </Button>
            </Fragment>
        )
    }
}


